import {
	mapState,
	mapMutations
} from 'vuex'
import uniIcon from "@/components/uni-icon.vue"
export default {
	components: {
		uniIcon
	},
	computed: mapState(['hasLogin','userHeadimg','userName']),
	data() {
		return {
			list: [
				{
					name: "个人资料",
					url: "personal/list",
					iconName: "icon-mn_yonghuziliao_fill"
				},
				{
					name: "咖啡钱包",
					url: "wallet/index",
					iconName: "icon-kafei"
				},
				{
					name: "优惠券",
					url: "coupon/index",
					iconName: "icon-youhuiquan"
				},
				{
					name: "兑换优惠",
					url: "cash/index",
					iconName: "icon-youhuiquan1"
				},
				{
					name: "发票管理",
					url: "invoice/index",
					iconName: "icon-fapiaosel"
				}
			]
		}
	},
	onLoad() {
		var info = uni.getStorageSync('info');
		if(info){
			 this.login();
			 this.changeUserName(info.userName);
		}
	},
	methods: {
		...mapMutations(['login','changeUserName']),
		go(url) {
			uni.navigateTo({
				url: url
			})
		},
		t() {
			this.login()
		}
	}
}
